.loader {
    width: 48px;
    height: 48px;
    display: inline-block;
    position: relative;
    color: #4d90fe;
    border: 1px solid;
    box-sizing: border-box;
    animation: fill 2s linear infinite alternate;
  }
  
  @keyframes fill {
    0% {
      box-shadow: 0 0  inset;
    }
    100% {
      box-shadow: 0 -48px inset;
    }
  }
      